<template>
  <section class="container accueil" ref="scrollParallax">
    

    <h1 class="titrePage d-flex justify-content-center"><Loading /></h1>

<scroll-parallax direction="y">
    <article class="articles">
      <router-link
        to="/club"
        title="Acceuil"
        class="accueil-article-link"
        style="text-decoration: none"
      >
        
          <h2 class="titre-link">Le Club</h2>
        
          <img
            id="imgSlider1"
            src="../assets/slider/club.jpg"
            alt="Club"
            title="go to Club's page"
            class="img-fluid"
          />
       
      </router-link>
    </article>
</scroll-parallax >
    <article
      class="articles actu col-lg-6 m-auto p-1 "
      v-for="article in articles"
      :key="article.id"
    >
      <section
        class="card-body text-dark text-left actu "
        id="articles"
      >
        <h2 class="d-flex m-4" v-if="article.article">Actus du club</h2>
        <div>
          <h4 v-if="article.articleTitle !== ''">
            {{ article.articleTitle }}
          </h4>
          <p v-if="article.articleChapo !== ''">
            <b>{{ article.articleChapo }}</b>
          </p>
          <p class="small" v-if="article.article !== ''">
            {{ article.article }}
          </p>
          <img
            class="w-100"
            :src="article.articleUrl"
            v-if="article.articleUrl !== ''"
          />
        </div>
      </section>
    </article>
<scroll-parallax direction="y">
    <article class="articles">
      
      <router-link
        to="/infoclub"
        title="Nos Cours"
        class="accueil-article-link"
        style="text-decoration: none"
      >
        
          <h2 class="titre-link">Nos Cours</h2>
          <img
            id="imgSlider1"
            src="@/assets/slider/info.jpg"
            alt="Nos cours"
            title="Nos Cours"
            class="img-fluid"
          />
      
      </router-link> 
     
    </article>
    <article class="articles col-lg-6 m-auto p-1 ">
      <!-- <SocialWall/> -->
      </article>
 </scroll-parallax>
    
  </section>
</template>

<script>
import axios from "axios";
import ScrollParallax from 'vue3-parallax/src/components/ScrollParallax.vue';
import Loading from "./Loading.vue"
// import SocialWall from "./SocialWall.vue"


export default {
  name: "index",
  components: {
     ScrollParallax,
     Loading,
    //  SocialWall
  },
  prop: {
      speed: {
        type: Number,
        required: true,
        default: 0.10
      },
      down: {
        type: Boolean,
        default: false,
        required: true
      },
      up: {
        type: Boolean,
        default: true,
        required: false
      },
      right: {
        type: Boolean,
        default: true,
        required: false
      },
      left: {
        type: Boolean,
        default: false,
        requiredequired: false
      },
      direction: {
        type: String,
        default: 'y',
        required: false
      }
  },

  data() {
    return {
       noMessage: false,
      isAdmin: false,
      isActive: true,
      newImage: "",
      currentUserId: "",
      newMessage: "",
      file: null,
      articles: [2],
      axes: null,
        speedCoeff: null
    };
  },
   methods: {
      loadParallax() {
        this.initDirection();
        this.el = this.$refs.scrollParallax;
        // const speedCoeff = this.down ? -this.speed : this.speed;
        window.addEventListener('scroll', () => {
          this.el.style.transform = `translate${this.axes}(${window.pageYOffset * this.speedCoeff}px)`;
        });
      },
      initDirection() {
         if (this.direction === 'y'){
          this.axes = 'Y' ;
          if (this.down) {
            this.speedCoeff = -this.speed;
          }
          else {
            this.speedCoeff = this.speed;
          }
        }
      }
    },
    mounted() {
      this.loadParallax();
    },

  created: function() {
    this.isAdmin = localStorage.getItem("role");
    this.currentUserId = localStorage.getItem("userId");
    if (localStorage.getItem("refresh") === null) {
      localStorage.setItem("refresh", 0);
      location.reload();
    }
    axios
      .get("http://127.0.0.1:3000/api/articles", {
        headers: { Authorization: "Bearer " + localStorage.getItem("token") }
      })
      .then(res => {
        const rep = res.data.ListeArticles;
        if (rep.length === 0) {
          this.noArticle = true;
        } else {
          this.noArticle = false;
        }
        this.articles = rep;
      });
  }
};
</script>

<style lang="scss">
