  padding-top: 15em;
}

.articles{
  margin: 0.2em 0em 15em 0em ;
}

.actu{
  margin: 10em 0em 0em 0em;
}

.titrePage{
  font-size: 3em;
}

.titre-link{
  display: flex;
  position: absolute;
  top: 1.5em;
  left: 1.5em;
  color: #ffffff;
  font-size: 2em;
}



// Small devices (landscape phones, 576px and up)
@media (min-width: 576px) and (max-width: 767.98px) {
 
