  margin: 0.2em 0em 15em 0em ;
}

.actu{
  margin: 10em 0em 0em 0em;
}

.titre-link{
  top: 3em;
  left: 2em;
  font-size: 2em;
}
}

// Large devices (desktops, 992px and up)
@media (min-width: 992px) and (max-width: 1199.98px) {
 

  .accueil{
  padding-top: 15em;
